import React from 'react';
import { Form, Input, Button,notification,} from 'antd';
import { useDispatch, useSelector } from 'react-redux';
import { actions } from '../../redux/actions';

export default function Donate(props) {
    const { card } = props;
    const dispatch = useDispatch();
    const user = useSelector(state => state.userReducer.user);
    const campaign = useSelector(state => state.campaignReducer.campaign);


    const onFinish = (values) => {
        notification.open({
            message: '转专 砖!',
            description:'转专 砖',
        });
        console.log('Received values of form: ', values);
        dispatch(actions.createDonation({ ...values, campaignId: campaign._id, user: user._id, card: card._id, date: Date.now() }));
        props.close();
    }
    return (
        <div className='Donate'>
            <Form onFinish={onFinish}>
                <div>{`转专 ${card.sum} 拽 ${card.gift ? card.gift.name : ' 砖 转'}`}</div>
                {/* <Form.Item
                    name="credutCard"
                    rules={[{ required: true, message: '住 住驻专 砖专!' }]}
                >
                    <Input type='text' placeholder='住驻专 砖专' />
                </Form.Item> */}
                <Form.Item>
                    <Button type="primary" htmlType="submit">转专</Button>
                </Form.Item>
            </Form>

        </div>
    )
}