import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Form, Input, Button } from 'antd';
import { actions } from '../../redux/actions';

export default function UserProfile(props) {

    const user = useSelector(state => state.userReducer.user);
    const dispatch = useDispatch();


    const onFinish = (values) => {
        console.log('Received values of form: ', values);
        dispatch(actions.updateUser({ ...user, ...values }));
        props.close();
    }

    return (
        <div className='UserProfile' style={{textAlign:'center'}}>
            <Form onFinish={onFinish} initialValues={user}>
                <Form.Item
                    name="email"
                    rules={[{ required: true, message: '住 !' }]}
                >
                    <Input placeholder="" />
                </Form.Item>
                <Form.Item
                    name="phone"
                    rules={[{ required: true, message: '住 驻!' }]}
                >
                    <Input placeholder="驻" />
                </Form.Item>
                <Form.Item
                    name="address"
                    rules={[{ required: true, message: '住 转转!' }]}
                >
                    <Input placeholder="转转" />
                </Form.Item>
                <Form.Item
                    name="name"
                    rules={[{ required: true, message: '住 砖!' }]}
                >
                    <Input placeholder="砖" />
                </Form.Item>
                <Form.Item>
                    <div className='d-flex justify-content-between'>
                        <Button type="primary" onClick={() => props.close()}></Button>
                        <Button type="primary" htmlType="submit">注</Button>
                    </div>
                </Form.Item>
            </Form>
        </div>
    )
}